.text36{
  font-size: 2.25em;
  line-height: 140%;
}

.text32{
  font-size: 2em;
}

.text24{
  font-size: 1.5em;
  line-height: 140%;
}

.text20{
  font-size: 1.25em;
  line-height: 140%;
}

.text18{
  font-size: 1.125em;
  line-height: 133%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.light-text{
  font-weight: 300;
}

.font2{
  font-family: $font2;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.gray-text2{
  color: $gray2;
}


@media screen and (max-width: 992px){
  .text36{
    font-size: 2em;
    line-height: 120%;
  }

  .text32,
  .text24{
    font-size: 1.125em;
    line-height: 133%;
  }

  .text20{
    font-size: 1em;
  }
}
