@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    background-image: url(../img/bg.svg);
    background-repeat: repeat-y;
    background-size: 100% auto;
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $black;
    font-size: calc(1200px * (16 / 1200));
    @media screen and (max-width: 1200px){
        font-size: calc(100vw * (16 / 1200));
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
    }
}

.container {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 0 1.875em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .m-page{
        font-size: calc(100vw * (16 / 768));
    }

    .container{
        padding: 0 1.25em;
        &._mob-full{
            padding: 0;
        }
    }

    .full-screen-page{
        height: calc(100 * var(--vh));
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 375));
    }
}


