.receipt{
  padding: 4.5em 0;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__img{
    width: 7.5em;
  }
  &__text{
    margin-top: 4em;
    font-family: $font2;
    font-weight: 700;
    @include grid100gap(1em);
  }
  &__title{
    font-size: 2.625em;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 115%;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
    margin: 3.25em auto 0;
    width: 29.25em;
    max-width: 100%;
  }
  &__nav{
    margin-top: 3.25em;
    width: 20.75em;
    max-width: 100%;
    @include grid100gap(1em);
  }
  &__btn{
    width: 100%;
    svg{
      width: 1.5em;
      height: auto;
      margin-right: .625em;
      flex-shrink: 0;
    }
  }
}

.receipt-item{
  padding: .75em .94em 1.25em;
  border-radius: 0.75em;
  background: #F9F9F9;
  &__img{
    padding-bottom: 100%;
    position: relative;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__num{
    margin: .94em auto 0;
    width: 4.8125em;
    height: 4.8125em;
    @include center;
    border: 1px solid #E3E3E3;
    border-radius: 50%;
    background-color: #fff;
    font-weight: 700;
    font-family: $font2;
    span{
      font-size: 1.7325em;
      line-height: 120%;
    }
  }
}


@media screen and (max-width: 992px){
  .receipt{
    padding: 1.25em 0;
    &__img{
      width: 5em;
    }
    &__text{
      margin-top: 1em;
      max-width: 18em;
      gap: .5em;
    }
    &__title{
      font-size: 1.25em;
      line-height: 120%;
    }
    &__desc{
      font-size: 1em;
      line-height: 150%;
    }
    &__items{
      margin-top: 1.5em;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .receipt-item{
    padding: 1em .66em;
    &__num{
      width: 3.75em;
      height: 3.75em;
      margin-top: 1em;
      span{
        font-size: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .receipt{
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: 1em;
    }
  }
}
