.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(#333, .5);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #fff;
        width: 41.25em;
        max-width: calc(100% - 2.5em);
        position: relative;
        z-index: 3;
        overflow: hidden;
        border-radius: 1.25em;
    }
    &__scroll{
        overflow-y: auto;
        overflow-x: hidden;
        max-height: calc(100 * var(--vh));
    }
    &__content{
        padding: 2.5em;
        border-top: 1px solid #F2F2F2;
    }
    &__question{
        display: flex;
        align-items: center;
        &-title{
            font-size: 1.25em;
            font-family: $font2;
            font-weight: 700;
        }
        &-price{
            margin-left: 1em;
            font-size: 1em;
            line-height: 115%;
            font-weight: 500;
            color: $primary;
        }
    }
}

.modal-product{
    display: grid;
    grid-template-columns: 6.25em calc(100% - 7.75em);
    gap: 1.5em;
    padding: 2.5em;
    &__img{
        img{
            width: 100%;
        }
    }
    &__content{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    &__col{
        @include grid100gap(1em);
        &:nth-child(2){
            min-width: 4.4375em;
            margin-left: 1em;
            flex-shrink: 0;
        }
    }
    &__title{
        font-family: $font2;
        font-size: 1.25em;
        line-height: 160%;
    }
    &__price{
        font-size: 1.75em;
        line-height: 115%;
        font-weight: 600;
    }
    &__contain,
    &__po,
    &__weight,
    &__kkal{
        font-size: 1em;
        color: $gray2;
    }
}

.additive{
    margin-top: 2em;
    @include grid100gap(1em);
}

.additive-card{
    display: grid;
    align-items: center;
    grid-template-columns: 6.25em calc(100% - 16em) 8.5em;
    gap: .625em;
    border-radius: 0.5em;
    border: 1px solid #E3E3E3;
    overflow: hidden;
    padding-right: 1.5em;
    &__img{
        img{
            width: 100%;
        }
    }
    &__content{
        padding: 1em 0;
        @include grid100gap(.875em);
    }
    &__title{
        font-size: 1.25em;
        font-weight: 700;
        font-family: $font2;
    }
    &__props{
        display: flex;
    }
    &__price,
    &__weight{
        font-size: .875em;
        line-height: 115%;
        font-weight: 500;
    }
    &__weight{
        margin-left: 1.14em;
        color: $gray2;
    }
    @media screen and (min-width: 577px){
        .calc:not(._added) ._plus{
            max-width: 3em;
            margin-left: auto;
        }
    }
}

.modal._added{
    .modal-nav{
        ._view1{
            display: none;
        }
        ._view2{
            display: flex;
        }
    }
}

.modal-nav{
    padding: 2.5em;
    border-top: 1px solid #f2f2f2;
    .m-btn{
        width: 100%;
    }
    ._view2{
        display: none;
    }
}


@media screen and (max-width: 992px){
    .modal{
        align-items: flex-end;
        &__dialog{
            max-width: 100%;
            width: 100%;
            border-radius: 1.25em 1.25em 0 0;
            max-height: 37em;
        }
        &__scroll{
            overflow: unset;
            overflow-x: hidden;
        }
        &__content{
            padding: 1.25em;
        }
    }

    .modal-product{
        padding: 1.25em;
        grid-template-columns: 6.25em calc(100% - 7.25em);
        gap: 1em;
        margin-bottom: 3.5em;
        position: relative;
        &__content{
            display: block;
        }
        &__col{
            &:nth-child(1){
                gap: .25em;
            }
            &:nth-child(2){
                display: flex;
                align-items: center;
                gap: unset;
                margin: .5em 0 0;
            }
        }
        &__title{
            font-size: 1em;
            line-height: 150%;
        }
        &__contain{
            font-size: .875em;
            line-height: 143%;
        }
        &__co{
            position: absolute;
            width: 100%;
            left: 0;
            top: 100%;
            height: 3.25em;
            @include center;
            text-align: center;
            border-top: 1px solid #F2F2F2;
        }
        &__price{
            font-size: 1.125em;
        }
        &__weight,
        &__kkal{
            font-size: .875em;
            margin-left: 1.14em;
        }
    }

    .modal-nav{
        padding: 1.25em;
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__content{
            padding-bottom: 0;
        }
        &__question{
            &-title{
                font-size: 1em;
            }
            &-price{
                font-size: .875em;
            }
        }
    }

    .additive{
        display: flex;
        gap: unset;
        width: calc(100% + 2.5em);
        padding: 0 1.25em;
        margin-left: -1.25em;
        margin-top: 1.5em;
        overflow-x: auto;
        &::-webkit-scrollbar {
            width: 0;
            height: 0;
        }
    }

    .additive-card{
        display: flex;
        gap: unset;
        align-items: center;
        flex-direction: column;
        padding: .5em 1em 1em;
        width: 9.3125em;
        flex-shrink: 0;
        margin-right: 1em;
        &__img{
            width: 6.25em;
            max-width: 100%;
        }
        &__content{
            padding: 0 0 1em;
        }
        &__title{
            font-size: 1em;
        }
        &__props{
            justify-content: center;
        }
        &__action{
            width: 100%;
        }
        .calc{
            height: 2.5em;
            justify-content: space-between;
            &__btn{
                width: 2.5em;
                padding: 0;
            }
            input{
                width: calc(100% - 5em);
            }
        }
    }

    .modal-nav{
        border-top: none;
        .m-btn{
            height: 3em;
        }
    }
}
