.order{
  &__grid{
    margin-top: 2.56em;
    display: grid;
    grid-template-columns: calc(100% - 24.5625em) 22.0625em;
    gap: 2.5em;
  }
  &__sidebar{
    .order-hint{
      padding: 1em;
      align-items: flex-start;
      border-color: $primary;
      .text16{
        line-height: 125%;
      }
    }
  }
  &__confirm{
    margin-top: 1.5em;
    &-text{
      font-size: .875em;
      line-height: 171%;
      a{
        color: #4D8AEB;
      }
    }
  }
}

.order-hint{
  padding: .375em .75em;
  min-height: 3em;
  display: flex;
  align-items: center;
  border-radius: 0.5em;
  border: 1px solid #E3E3E3;
  background: #FFF;
  margin-bottom: 1em;
  img{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .625em;
  }
}

.cart{
  @include grid100gap(1em);
}

.cart-card{
  padding-right: 1.5em;
  display: grid;
  grid-template-columns: 8.5em calc(100% - 14.5em) 3em;
  gap: 1.5em;
  align-items: center;
  border-radius: 1em;
  border: 1px solid #E3E3E3;
  background-color: #fff;
  position: relative;
  &__num{
    position: absolute;
    top: .8125em;
    left: .8125em;
    @include center;
    border-radius: 50%;
    width: 3.125em;
    height: 3.125em;
    background-color: #fff;
    border: 1px solid #E3E3E3;
    font-weight: 700;
    font-family: $font2;
    span{
      font-size: 1.125em;
      line-height: 120%;
    }
  }
  &__img{
    height: 100%;
    @include center;
    border-radius: 1em 0 0 1em;
    overflow: hidden;
    background: #F9F9F9;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.875em);
  }
  &__price,
  &__title{
    font-size: 1.125em;
    line-height: 115%;
  }
  &__title{
    font-family: $font2;
    font-weight: 700;
  }
  &__price{
    font-weight: 600;
  }
  &__status,
  &__desc{
    font-size: 1em;
    color: $gray2;
  }
  &__status{
    line-height: 115%;
  }
  &__btn{
    width: 3em;
    height: 3em;
    @include center;
    svg{
      width: 1.5em;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  .cart-card__status{
    display: none;
  }
  &._deleted{
    .cart-card__img img{
      opacity: 0.5;
    }
    .cart-card__status{
      display: block;
    }
    .cart-card__num,
    .cart-card__price{
      display: none;
    }
    .cart-card__btn{
      svg{
        transform: rotate(-45deg);
      }
    }
  }
}

.order-detail{
  border: 1px solid #F2F2F2;
  border-radius: 1em;
  background-color: #fff;
  margin-bottom: 1.25em;
  &__sum{
    padding: 1.25em 1.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 600;
    &-title{
      font-size: 1em;
      line-height: 150%;
    }
    &-num{
      font-size: 1.25em;
      line-height: 120%;
    }
  }
  &__items{
    border-top: 1px solid #F2F2F2;
    border-bottom: 1px solid #F2F2F2;
    padding: 1.5em;
    @include grid100gap(.5em);
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-title,
    &-desc{
      font-size: 1em;
      line-height: 150%;
    }
  }
  &__promo{
    padding: 1.5em;
  }
}

.order-nav{
  margin-bottom: 1.5em;
  .m-btn{
    width: 100%;
  }
}

.order-header{
  display: none;
}


@media screen and (max-width: 992px){
  .order{
    &__grid{
      grid-template-columns: 100%;
    }
    &__sidebar{
      .order-hint{
        .text16{
          line-height: 150%;
        }
      }
    }
  }

  .order-page{
    .header{
      display: none;
    }
  }

  .order-header{
    display: block;
    z-index: 10;
    width: 100%;
    background-color: #fff;
  }
}



@media screen and (max-width: 576px){
  .order{
    &__grid{
      margin-top: .5em;
      gap: 0;
    }
    &__sidebar{
      .order-hint{
        padding: .75em;
        .text16{
          line-height: 129%;
        }
      }
    }
  }

  .order-hint{
    align-items: flex-start;
    padding: .75em;
    img{
      width: 1.25em;
    }
    .text16{
      font-size: .875em;
      line-height: 129%;
    }
  }

  .cart{
    margin-top: .75em;
    gap: 0;
  }

  .order-detail,
  .cart{
    margin-left: -1.25em;
    width: calc(100% + 2.5em);
  }

  .cart-card{
    padding: 1.2em;
    border-radius: 0;
    border-left: none;
    border-right: none;
    grid-template-columns: 5em calc(100% - 8.5em) 1.5em;
    gap: 1em;
    &:not(:last-child){
      border-bottom: none;
    }
    &__num{
      top: .5em;
      left: .5em;
      width: 2em;
      height: 2em;
      span{
        font-size: .875em;
      }
    }
    &__img{
      height: 5em;
      border-radius: 0;
    }
    &__content{
      gap: .25em;
    }
    &__title{
      font-size: 1em;
      line-height: 150%;
    }
    &__desc{
      font-size: .875em;
      margin-bottom: .25em;
    }
    &__btn{
      width: 1.5em;
      height: 1.5em;
    }
  }

  .order-detail{
    border-radius: 0;
    border-top: none;
    border-left: none;
    border-right: none;
    display: flex;
    flex-direction: column;
    &__items,
    &__sum,
    &__promo{
      padding: 1.5em 1.25em;
    }
    &__items{
      order: -1;
      border-top: none;
    }
    &__promo{
      border-top: 1px solid #f2f2f2;
    }
  }

  .order-nav{
    margin: 1.25em 0 1.5em;
  }
}
