.main{
  padding: 1.875em 0 1.25em;
  &__inner{
    position: relative;
    min-height: 19.875em;
    padding: 3.125em 2.5em;
  }
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      border-radius: .75em;
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
    max-width: 27.8em;
    color: #fff;
    @include grid100gap(1.125em);
  }
  .text36{
    span{
      white-space: nowrap;
    }
  }
  .text24{
    opacity: .7;
  }
}

.sticky-footer{
  box-shadow: 0 .6875em 2.5em 0 rgba(0, 0, 0, 0.08);
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 1.5em 0;
  background-color: #fff;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    width: 17.0625em;
    flex-shrink: 0;
  }
  &__price{
    color: #828282;
    margin-left: .5em;
  }
}

.location{
  padding: 1.25em 0 7.5em;
}

.map{
  height: 22em;
}

.addresses{
  margin-bottom: 2em;
}

.addresses-slider{
  margin-top: 2em;
  .swiper{
    &-slide{
      width: auto;
      padding-right: .69em;
    }
    &-scrollbar{
      border-radius: .4375em;
      background-color: #F2F2F2;
      height: .25em;
      margin-top: 2em;
      &-drag{
        background: $primary;
        border-radius: inherit;
        &:before{
          content: '';
          position: absolute;
          height: 500%;
          width: 100%;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
        }
      }
    }
  }
}

.address-card{
  position: relative;
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
    &:checked+.address-card__inner:before{
      border-color: $black;
      border-width: 2px;
    }
  }
  &__inner{
    padding: 1em;
    position: relative;
    background-color: #fff;
    border-radius: .75em;
    &:before{
      pointer-events: none;
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 1px solid #E3E3E3;
      border-radius: .75em;
    }
  }
  &__top{
    display: flex;
    align-items: center;
    white-space: nowrap;
  }
  &__name,
  &__bus,
  &__desc{
    font-size: 1em;
  }
  &__name{
    font-family: $font2;
    font-weight: 700;
  }
  &__bus{
    margin-left: 1em;
    color: $gray2;
  }
  &__desc{
    margin-top: .25em;
    color: $gray2;
    font-weight: 300;
    line-height: 150%;
  }
}

.location-modal{
  overflow: hidden;
  &__top,
  &__bottom{
    display: none;
  }
}

.address-mob{
  display: none;
}


@media screen and (max-width: 992px){
  .main{
    padding: 0;
    .container{
      padding: 0;
    }
    &__inner{
      padding: 0;
    }
    &__content{
      position: absolute;
      top: 0;
      left: 0;
      padding: 2em;
      gap: .69em;
      max-width: 28.8em;
    }
    &__bg{
      position: relative;
      height: unset;
      height: calc(100 * var(--vh) - 10em);
      overflow: hidden;
      img{
        border-radius: 0;
        position: absolute;
        bottom: 0;
        left: 0;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .location{
    &._hide-mob{
      display: none;
    }
  }

  .location-modal{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    background-color: #fff;
    z-index: 100;
    &._active{
      opacity: 1;
      pointer-events: unset;
    }
    &__top{
      display: flex;
      align-items: center;
      height: 3.75em;
      padding: 0 1.25em;
      border-bottom: 1px solid #E3E3E3;
    }
    &__back{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: 1em;
      svg{
        width: 100%;
        height: auto;
      }
    }
    &__title{
      font-size: 1em;
    }
    &__view{
      margin-left: auto;
      width: 1.5em;
      height: 1.5em;
      flex-shrink: 0;
      position: relative;
      svg{
        width: 100%;
        height: auto;
        @include centerAbsolute;
      }
      ._view2{
        opacity: 0;
        pointer-events: none;
      }
    }
    .location{
      padding: 0;
      &__inner{
        display: flex;
        flex-direction: column;
        align-items: stretch;
        height: calc(100 * var(--vh) - 9.75em);
      }
      .container{
        padding: 0;
      }
    }
    .map{
      height: calc(100% - 6em);
      order: -1;
    }
    &__bottom{
      display: flex;
      padding: 1em;
      .m-btn{
        width: 100%;
      }
    }
    ._view2{
      opacity: 0;
      pointer-events: none;
    }
    &._map-view{
      ._view1{
        opacity: 0;
        pointer-events: none;
      }
      ._view2{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .addresses{
    margin: 0;
    padding: 1.25em 1.25em 0;
    .text24{
      display: none;
    }
  }

  .addresses-slider{
    margin: 0;
    .swiper{
      &-scrollbar{
        display: none;
      }
      &-slide{
        width: 16em;
        padding-right: 1em;
      }
    }
  }

  .address-card{
    &__desc{
      @include truncate(1);
    }
  }

  .address-mob{
    width: 100%;
    padding: 1.25em;
    display: block;
    height: calc(100 * var(--vh) - 9.75em);
    overflow-y: auto;
    position: absolute;
    left: 0;
    top: 3.75em;
    &__items{
      width: 100%;
      @include grid100gap(1em);
    }
  }
}


@media screen and (max-width: 576px){
  .main{

  }

  .sticky-footer{
    padding: 1.25em 0;
    &__inner{
      display: block;
    }
    &__title{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 1em;
    }
    ._hide-mob{
      display: none;
    }
    &__btn{
      width: 100%;
    }
  }
}
