.header{
  z-index: 10;
  position: relative;
  padding: 1.19em 0;
  background-color: $gray;
  border-bottom: 1px solid #F2F2F2;
  &__mob{
    display: none;
  }
  &__logo{
    display: block;
    width: 6.625em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.menu{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__left{
    display: flex;
    align-items: center;
  }
  &__right{
    display: flex;
    align-items: center;
  }
  &__bg{
    display: none;
  }
  &__phone{
    margin-left: auto;
    font-size: 1em;
    font-weight: 500;
  }
  &__links,
  &__offer{
    display: none;
  }
}

.lang{
  display: flex;
  align-items: center;
  margin-left: 1em;
  &__item{
    font-size: 1em;
    margin-left: 1.44em;
    color: rgba($black,.5);
    &._active{
      color: $black;
      pointer-events: none;
    }
    ._view2{
      display: none;
    }
  }
}

.city-select{
  margin-left: 2.75em;
  position: relative;
  &._active{
    .city-select__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    transition: all .3s;
    svg{
      width: 1.5em;
      height: auto;
      margin-left: .25em;
      flex-shrink: 0;
    }
    &:hover{
      cursor: pointer;
      color: $primary;
      path{
        fill: $primary;
      }
    }
  }
  &__hidden{
    position: absolute;
    top: 100%;
    left: 0;
    min-width: 100%;
    border: 1px solid #F2F2F2;
    background-color: #fff;
    margin-top: .25em;
    padding: .5em .75em;
    opacity: 0;
    pointer-events: none;
  }
  &__items{
    @include grid100gap(.5em);
  }
  &__item{
    white-space: nowrap;
    font-size: 1em;
    line-height: 130%;
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: 1.12em 0;
    &__mob{
      display: flex;
      align-items: center;
    }
  }

  .m-page{
    &._open{
      .menu__bg{
        display: block;
      }
      .menu{
        transform: translateX(0);
      }
    }
  }

  .burger{
    width: 1.5em;
    margin-right: 1em;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: auto;
    }
  }

  .menu{
    position: fixed;
    height: calc(100* var(--vh));
    top: 0;
    left: 0;
    z-index: 20;
    width: 15.625em;
    background-color: #fff;
    flex-direction: column;
    align-items: stretch;
    overflow-y: auto;
    transition: all .3s;
    transform: translateX(-120%);
    &__bg{
      z-index: 15;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: calc(100* var(--vh));
      background-color: rgba($black,.5);
    }
    .header__logo{
      padding: 2.06em 2.69em;
      width: 100%;
      border-bottom: 1px solid #F2F2F2;
    }
    &__right{
      margin-top: auto;
      padding: 1.25em;
      display: block;
    }
    &__phone{
      margin: 1.25em;
      border: 1px solid #E3E3E3;
      height: 2.5em;
      border-radius: 0.375em;
      @include center;
      &:hover{
        border-color: $primary;
      }
    }
    &__links{
      display: grid;
      grid-template-columns: 4.375em calc(100% - 5.375em);
      gap: 1em;
      margin-bottom: 1em;
    }
  }

  .menu__link,
  .menu__offer,
  .city-select__item,
  .lang__item{
    padding: 0 .75em;
    border-radius: 0.25em;
    border: 1px solid #E3E3E3;
    height: 2.25em;
    font-weight: 500;
    @include center;
    &:hover{
      border-color: $primary;
    }
    &._active{
      border-width: 2px;
    }
    &._red{
      border-color: $primary;
      color: $primary;
    }
  }

  .city-select{
    margin: 0;
    width: 100%;
    padding: 1.25em;
    border-bottom: 1px solid #F2F2F2;
    &__show{
      display: none;
    }
    &__hidden{
      opacity: 1;
      pointer-events: unset;
      position: static;
      border: none;
      padding: 0;
      margin: 0;
      width: 100%;
    }
    &__items{
      display: flex;
      gap: unset;
      flex-wrap: wrap;
      margin-top: -1em;
    }
    &__item{
      margin: 1em 1em 0 0;
    }
  }

  .lang{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    margin: 1em 0 0;
    width: 100%;
    &__item{
      margin: 0;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
