@import "../vars.scss";
.breadcrumbs {
    padding: 2.25em 0 2.5em;
    .text32{
        margin-top: .75em;
    }
    &__item {
        font-size: 1em;
        line-height: 100%;
        position: relative;
        font-weight: 400;
        display: inline;
        color: #828282;
        &:last-child {
            color: #B3B3B3;
        }
        &:not(:first-child) {
            margin-left: 2em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: 1.5em;
                height: 1.5em;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
                top: -.125em;
                right: 100%;
                margin-right: .275em;
            }
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: 1em 0;
        &__items{
            display: none;
        }
        .text32{
            margin: 0;
        }
    }
}
