.catalog{
  padding-bottom: 7.5em;
  &__btn{
    display: none;
  }
  &__inner{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 2.5em;
  }
}

.catalog-card{
  background-color: #fff;
  border: 2px solid #F2F2F2;
  border-radius: 1em;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  &__top{
    position: relative;
  }
  &__badge{
    position: absolute;
    bottom: 1.25em;
    left: 1.25em;
    width: 4em;
    display: block;
  }
  &__img{
    padding-bottom: 100%;
    position: relative;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__content{
    padding: 1.75em 2em;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__info{
    @include grid100gap(.75em);
    .text16{
      line-height: 100%;
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.5em;
  }
  &__prices{
    display: flex;
    align-items: center;
    .text16{
      margin-left: 1.25em;
    }
  }
  .calc{
    margin-top: 1.5em;
  }
  &__empty{
    margin-top: 1.5em;
    height: 3em;
  }
  &._disabled{
    opacity: .7;
    pointer-events: none;
    .catalog-card__img{
      filter: grayscale(100%);
    }
  }
}

.calc{
  width: 100%;
  height: 3em;
  display: flex;
  &__btn{
    width: 3em;
    height: 100%;
    svg{
      width: 1.5625em;
      height: auto;
    }
  }
  input{
    height: 100%;
    font-size: 1.125em;
    text-align: center;
    width: calc(100% - 5.33em);
    border: none;
    background-color: transparent;
  }
  &:not(._added){
    ._minus,
    input{
      padding: 0;
      flex-grow: 0;
      width: 0;
      overflow: hidden;
    }
    ._plus{
      flex-grow: 1;
    }
  }
}

.catalog-empty{
  &__inner{
    margin: 0 auto;
    max-width: 100%;
    width: 27.4375em;
    padding: 4.5em 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__img{
    width: 7.5em;
    display: block;
  }
  &__info{
    padding: 2.5em 2em 0;
    @include grid100gap(1em);
  }
  &__nav{
    width: 100%;
    margin-top: 3.25em;
    .m-btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    &__btn{
      width: 22em;
      height: 3em;
      max-width: 100%;
      border-radius: .375em;
      display: flex;
    }
    &__inner{
      margin-top: 1em;
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: 1em;
    }
  }

  .catalog-card{
    border: none;
    border-radius: .75em;
    &__badge{
      bottom: .75em;
      left: .75em;
      width: 2.4375em;
    }
    &__content{
      padding: .75em;
    }
    &__info{
      .text16{
        display: none;
      }
    }
    &__bottom{
      padding-top: 1em;
    }
    &__prices{
      .text16{
        font-size: .875em;
        margin-left: 1.42em;
      }
    }
    &__empty,
    .calc{
      margin-top: 1em;
    }
  }

  .catalog-empty{
    .breadcrumbs{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    padding-bottom: 9em;
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .5em));
    }
  }

  .catalog-empty__inner,
  .catalog-empty,
  .catalog-empty .container{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }

  .catalog-empty{
    &__inner{
      padding: 8em 0 1.25em;
    }
    &__img{
      width: 5em;
    }
    &__info{
      gap: .5em;
      .text18{
        font-size: 1em;
      }
    }
    &__nav{
      margin-top: auto;
    }
  }
}
