@import "../vars";
.m-btn   {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    border-radius: .5em;
    padding: 0 1.5em;
    height: 3.75em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
        line-height: 150%;
        letter-spacing: -0.0225em;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: #F22A2A;
        color: #fff;
    }
    &:active{
        background-color: #E82020;
        color: #fff;
    }
}

.m-btn-gray{
    background-color: #F2F2F2;
    color: $black;
    &:hover{
        background-color: darken(#F2F2F2,10%);
        color: $black;
    }
    &:active{
        background-color: darken(#F2F2F2,20%);
        color: $black;
    }
}
